<script>
  export default {
    name: 'SignalCard',
    filters: {
      toRawString (input) {
        if (input) {
          return input.map(entry => {
            let preliminaryResult = entry.toString(16)
            let result = ''
            if (preliminaryResult.length < 2) {
              result = "0" + preliminaryResult
            } else {
              result = preliminaryResult
            }
            return result }).toString()
        }
      },
    },
    props: {
      props: { type: Object, default: null },
    },
    data () {
      return {
        showAdditionalInfo: false,
        showConfiguration: false,
        radius: 1,
        padding: 16,
        lineWidth: 1,
        dataHistory: 100,
        chartType: 'trend',
        typeOptions: ['trend', 'bar'],
        chartDataValueArray: [100,0,100,0,10,50,70,20,100],
      }
    },
    computed: {
      chartDataValues () {
        // props.item.data
        return this.chartDataValueArray
      },
    },
    watch: {
      props () {
        this.chartDataValueArray.push(this.props.item.data)
        if (this.chartDataValueArray.length > this.dataHistory) {
          this.chartDataValueArray.shift()
        }
      },
    },
    methods: {
    },
  }
</script>
