<template>
  <div>
    <VCard class="marginToolbar">
      <VToolbar
        card
        color="transparent"
        extended
      >
        <VIcon>
          gamepad
        </VIcon>
        <VToolbarTitle>
          Control
        </VToolbarTitle>
        <VSpacer />
        <!-- <template v-slot:extension>
        <VTextField
          v-model="search"
          label="Search signals"
          flat
          solo
          hide-details
          clearable
          prepend-inner-icon="search"
        />
        <VCheckbox
          v-model="caseSensitive"
          dark
          hide-details
          label="Case sensitive"
        />
      </template> -->
      </VToolbar>
      <VLayout>
        <VFlex xs12>
          <VCardText class="overflowY">
            <div
              v-if="selectedSignals.length === 0"
              key="title"
              class="title font-weight-light grey--text pa-3 text-xs-center"
            >
              You have no selected signals at this time.
            </div>
            <VScrollXTransition
              group
              hide-on-leave
            >
              <VChip
                v-for="(selection, i) in selectedSignals"
                v-show="(selection && selection.name !== 'Signals' && selection.name[0] !== '!' && selection.name.length > 0)"
                :key="i"
                small
              >
                {{ selection.name }}
              </VChip>
            </VScrollXTransition>
          </VCardText>
        </VFlex>
      </VLayout>
      <VDivider />
      <VCardActions>
        <VBtn
          flat
          depressed
          to="selection"
        >
          Signals
          <VIcon right>
            ballot
          </VIcon>
        </VBtn>
        <VSpacer />
        <VBtn
          v-if="!subscribed"
          :disabled="selectedSignals.length === 0"
          color="green"
          depressed
          @click="streamSetup"
        >
          Subscribe
          <VIcon right>
            cast
          </VIcon>
        </VBtn>
        <VBtn
          v-else
          color="error"
          depressed
          @click="stopStream"
        >
          Unsubscribe
          <VIcon right>
            cast_connected
          </VIcon>
        </VBtn>
      </VCardActions>
    </VCard>
    <VContainer
      fluid
      grid-list-xl
      mt-3
      px-0
    >
      <VDataIterator
        :items="signalDataList"
        content-class="layout row wrap"
        row
        wrap
        :rows-per-page-items="rowsPerPageItems"
        :pagination.sync="pagination"
      >
        <template v-slot:item="props">
          <SignalCard
            :props="props"
            min="props.item.min"
            max="props.item.max"
            size="props.item.size"
          />
        </template>
      </VDataIterator>
    </VContainer>
  </div>
</template>
<script>
  import './../grpc/dist/api.js'
  import SignalCard from "./../components/SignalCard.vue"
  export default {
    name: "Control",
    components: {SignalCard},
    data: () => { return {
      subscribed: false,
      request: '',
      search: null,
      items: [],
      caseSensitive: false,
      stream: null,
      NetworkService: null,
      subsConfig: null,
      rowsPerPageItems: [6, 12, 24],
      pagination: {
        rowsPerPage: 6, // infinite rows per page
        descending: false, // sorting order
        sortBy: "name", // sort field for pagination
      },
      // signalDataList: [],
    } },
    computed: {
      signalDataList: {
        get () {
          return this.$store.state.signalDataList
        },
        set (value) {
          this.$store.commit('updateSignalDataList', value)
        },
      },
      connectionStatus: {
        get () {
          return this.$store.state.connectionStatus
        },
        set (value) {
          this.$store.commit('updateConnectionStatus', value)
        },
      },
      selectedSignals: {
        get () {
          return this.$store.state.selectedSignals
        },
        set (value) {
          this.$store.commit('updateSelectedSignals', value)
        },
      },
      requestHistory: {
        get () {
          return this.$store.state.requestHistory
        },
        set (value) {
          this.$store.commit('updateRequestHistory', value)
        },
      },
      filter () {
        return this.caseSensitive
          ? (item, search, textKey) => { return item[textKey].indexOf(search) > -1 }
          : undefined
      },
    },
    watch: {
    },
    created () {
    },
    beforeDestroy(){
      this.stopStream()
    },
    methods: {
      streamSetup () {
        // eslint-disable-next-line no-undef
        this.NetworkService = new api.default.NetworkServiceClient(this.$store.state.brokerServerIp)
        // eslint-disable-next-line no-undef
        this.subsConfig = new api.default.SubscriberConfig()
        // this.streamSetup()
        this.request = 'Subscribe'
        // create empty array to contain signals
        const signals = [];
        this.selectedSignals.forEach(signal => {
          if (signal.signalId){
            signals.push(signal.signalId)
          }
        })
        // eslint-disable-next-line no-undef
        const signalIds = new api.default.SignalIds()
        signalIds.setSignalidList(signals)
        // eslint-disable-next-line no-undef
        const clientId = new api.default.ClientId()
        clientId.setId("my_client")
        this.subsConfig.setSignals(signalIds)
        this.subsConfig.setClientid(clientId)
        // this.subsConfig.setMessage(this.request);
        // this.subsConfig.setMessageCount(0);
        // this.subsConfig.setMessageInterval(-1);
        // subsConfig.setOnchange(false)
        this.startStream()
      },
      startStream () {
        this.stream = this.NetworkService.subscribeToSignals(this.subsConfig)
        this.stream.on('data', response => { this.streamResponse(response)
                                             this.connectionStatus = 'success--text'
        });
        this.stream.on('end', () => { this.stopStream()
        // this.connectionStatus = 'error--text'
        })
        this.stream.on('status', (status) => { console.log('status', status.metadata); })
        this.stream.on('error', (error) => { this.stopStream();
        // this.connectionStatus = 'error--text'
        })
        this.$store.commit('updateRequestHistory', this.requestHistory)
        // setTimeout(() => {
        //   this.stopStream()
        // }, 4000);
      },
      stopStream () {
        this.request = 'Cancel Sub';
        if (this.stream){
          this.stream.cancel()
        }
        this.subscribed = false;
        this.$store.commit('updateRequestHistory', this.requestHistory)
      },
      streamResponse (response) {
        this.subscribed = true;
        const signalList = response.getSignalList()
        let streamResult = []
        signalList.forEach(signal => {
          let signalData = ''
          let dataType = ''
          const nameSpace = signal.getId().getNamespace()
          if (signal.hasDouble()) {
            signalData = signal.getDouble()
            dataType = 'Double'
          } else if (signal.hasInteger()) {
            signalData = signal.getInteger()
            dataType = 'Integer'
          } else if (signal.hasArbitration()) {
            signalData = signal.getArbitration()
            dataType = 'Arbitration'
          } else if (signal.hasEmpty()) {
            signalData = signal.getEmpty()
            dataType = 'Empty'
          }
          const name = signal.getId().getName()
          streamResult.push({
            name: name,
            id: name,
            data: signalData,
            dataType: dataType,
            nameSpace: nameSpace,
            unit: signal.unit,
            max: signal.max,
            min: signal.min,
            size: signal.size,
            raw: signal.getRaw(),
          })
        });
        this.signalDataList = streamResult
      },
    },
  }
</script>
<style scoped>
.overflowY {
  overflow-y: auto;
  max-height: calc(58vh - 228px);
}
.marginToolbar {
  margin-top: -64px;
}
</style>
